@font-face {
  font-family: "Montserrat Alternates";
  src: local("montserrat-alternates-regular"), url("../fonts/montserrat-alternates-regular.woff2") format("woff2"), url("../fonts/montserrat-alternates-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat Alternates";
  src: local("montserrat-alternates-medium"), url("../fonts/montserrat-alternates-medium.woff2") format("woff2"), url("../fonts/montserrat-alternates-medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat Alternates";
  src: local("montserrat-alternates-semibold"), url("../fonts/montserrat-alternates-semibold.woff2") format("woff2"), url("../fonts/montserrat-alternates-semibold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat Alternates";
  src: local("montserrat-alternates-bold"), url("../fonts/montserrat-alternates-bold.woff2") format("woff2"), url("../fonts/montserrat-alternates-bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat Alternates";
  src: local("montserrat-alternates-extrabold"), url("../fonts/montserrat-alternates-extrabold.woff2") format("woff2"), url("../fonts/montserrat-alternates-extrabold.woff") format("woff");
  font-weight: 800;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat Alternates";
  src: local("montserrat-alternates-black"), url("../fonts/montserrat-alternates-black.woff2") format("woff2"), url("../fonts/montserrat-alternates-black.woff") format("woff");
  font-weight: 900;
  font-style: normal;
  font-display: swap;
}
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
}

a {
  text-decoration: none;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
}

img {
  max-width: 100%;
  display: block;
  height: auto;
}

@keyframes bounce {
  0% {
    transform: translateY(-2000px);
  }
  70% {
    transform: translateY(30px);
  }
  90% {
    transform: translateY(-10px);
  }
  to {
    transform: translateY(0);
  }
}
@keyframes shake {
  0%, 100% {
    transform: translateX(0);
  }
  10%, 30%, 50%, 70%, 90% {
    transform: translateX(-10px);
  }
  20%, 40%, 60%, 80% {
    transform: translateX(10px);
  }
}
.page {
  height: 100%;
  scroll-behavior: smooth;
  background-color: #1d1d1e;
}

.page__body {
  position: relative;
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
}
.page__body--hidden {
  overflow: hidden;
}

.header {
  flex: 0 0 auto;
}

.header__wrapper {
  display: flex;
  max-width: 1280px;
  padding: 0 49px;
  margin: 0 auto;
  min-height: 59px;
  align-items: center;
}
@media (max-width: 1279px) {
  .header__wrapper {
    justify-content: space-between;
  }
}

.header__nav {
  display: flex;
}

.header__logo {
  margin-right: 70px;
}

.nav {
  position: relative;
}
.nav__list,
.nav__contact-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__list {
  margin-right: 70px;
}
@media (max-width: 1279px) {
  .nav__list {
    flex-direction: column;
    margin: 0;
    align-items: center;
  }
}

@media (max-width: 1279px) {
  .nav__contact-list {
    margin-top: auto;
    margin-bottom: 30px;
  }
}

.nav__wrapper {
  display: flex;
  align-items: center;
}
@media (max-width: 1279px) {
  .nav__wrapper {
    width: 300px;
    min-height: 400px;
    opacity: 0;
    visibility: hidden;
    flex-direction: column;
    position: absolute;
    z-index: 1;
    right: 0;
    background-color: #1d1d1e;
  }
  .nav__wrapper--show {
    opacity: 1;
    transition: 0.3s ease-in-out;
    visibility: visible;
    padding-top: 60px;
  }
}

.nav__item {
  font-family: "Montserrat Alternates", "Arial", sans-serif;
  font-size: 14px;
  line-height: 17px;
  font-weight: 400;
  font-style: normal;
  padding: 4px 15px;
}
@media (max-width: 1279px) {
  .nav__item {
    margin-bottom: 10px;
  }
}
.nav__item a {
  color: #ffffff;
}
.nav__item:hover, .nav__item:focus-visible {
  outline: 1px solid #fed524;
  transition: 0.3s ease-in-out;
}
.nav__item:hover a, .nav__item:focus-visible a {
  color: #fed524;
  transition: 0.3s ease-in-out;
}

.nav__contact-item {
  margin-right: 8px;
  transition: 0.3s ease-in-out;
}
@media (max-width: 1279px) {
  .nav__contact-item:last-child {
    margin-right: 0;
  }
  .nav__contact-item:hover span, .nav__contact-item:focus-visible span {
    transition: 0.3s ease-in-out;
    opacity: 1;
    visibility: visible;
    width: auto;
    height: auto;
  }
  .nav__contact-item:hover:last-child, .nav__contact-item:focus-visible:last-child {
    margin-right: 8px;
  }
}
.nav__contact-item a {
  display: flex;
  align-items: center;
  font-family: "Montserrat Alternates", "Arial", sans-serif;
  font-size: 14px;
  line-height: 17px;
  font-weight: 400;
  font-style: normal;
  color: #ffffff;
}
.nav__contact-item span {
  padding-left: 7px;
}
@media (max-width: 1279px) {
  .nav__contact-item span {
    width: 0;
    height: 0;
    visibility: hidden;
    opacity: 0;
  }
}
.nav__contact-item:hover span, .nav__contact-item:focus-visible span {
  transition: 0.3s ease-in-out;
  color: #fed524;
}

.main {
  flex: 1 0 auto;
  overflow: hidden;
  transition: 0.3s ease-in-out;
}

