}
.how-it-works__title h2 {
  margin: 0;
  font-family: "Montserrat Alternates", "Arial", sans-serif;
  font-size: 48px;
  line-height: 59px;
  font-weight: 800;
  font-style: normal;
  color: #e8e8e8;
}

.how-it-works__list {
  display: flex;
  flex-flow: row wrap;
  margin-top: 144px;
  justify-content: center;
  padding: 0;
  counter-reset: section;
  list-style-type: none;
}

.how-it-works__item {
  width: 340px;
