.how-it-works__item h3 {
  margin: 0;
  font-family: "Montserrat Alternates", "Arial", sans-serif;
  font-size: 20px;
  line-height: 24px;
  font-weight: 900;
  font-style: normal;
  color: #fed523;
  text-align: right;
}
@media (min-width: 1181px) {
  .how-it-works__item:nth-child(3n+3) {
