  bottom: -76px;
}

.how-it-works__content {
  border-top: 5px solid #e8e8e8;
  margin-top: 98px;
  padding-top: 15px;
  padding-right: 56px;
}
.how-it-works__content p {
  font-family: "Montserrat Alternates", "Arial", sans-serif;
  font-size: 16px;
  line-height: 20px;
  font-weight: 600;
