  position: relative;
}
.form p {
  font-family: "Montserrat Alternates", "Arial", sans-serif;
  font-size: 40px;
  line-height: 49px;
  font-weight: 800;
  font-style: normal;
  color: #000000;
  margin: 0;
  text-align: center;
}
.form::before {
  position: absolute;
  content: url("../img/decor/decor-coin-form.svg");
  top: 207px;
