.form__fields-item input:hover, .form__fields-item input:focus-visible,
.form__fields-item textarea:hover,
.form__fields-item textarea:focus-visible {
  transition: 0.3s ease-in-out;
  border-color: #c06903;
}
.form__fields-item input:hover::placeholder, .form__fields-item input:focus-visible::placeholder,
.form__fields-item textarea:hover::placeholder,
.form__fields-item textarea:focus-visible::placeholder {
  transition: 0.3s ease-in-out;
  color: #000000;
}
.form__fields-item input {
  min-height: 43px;
  margin-bottom: 21px;
}
.form__fields-item textarea {
  resize: none;
  min-height: 120px;
  padding-top: 12px;
  padding-bottom: 20px;
}

.footer {
  flex: 0 0 auto;
  padding-top: 212px;
  position: relative;
}
.footer::before {
  position: absolute;
  content: url("../img/decor/decor-coin-big.svg");
  top: 14px;
  left: calc((100% - 1587px) / 2);
  z-index: -1;
}

.footer__wrapper {
  width: 100%;
  min-height: 216px;
  background-color: #ffffff;
  padding: 55px 0 35px;
}

.footer__content {
  width: 543px;
  min-height: 106px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  position: relative;
}

.footer__contact-list,
.footer__social-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.footer__contact-item {
  margin-bottom: 20px;
}
.footer__contact-item a {
  font-family: "Montserrat Alternates", "Arial", sans-serif;
  font-size: 18px;
  line-height: 22px;
  font-weight: 600;
  font-style: normal;
  color: #000000;
}
.footer__contact-item a:hover, .footer__contact-item a:focus-visible {
  transition: 0.3s ease-in-out;
  color: #c06903;
}

.footer__social-list {
  position: absolute;
  display: flex;
  justify-content: space-between;
  width: 179px;
  right: 0;
  bottom: 20px;
}

.footer__social-item:hover img {
  transition: 0.3s ease-in-out;
  transform: scale(1.2);
}

.container {
  width: 100%;
  max-width: 1211px;
  margin: 0 auto;
  padding: 0 20px;
}
@media (max-width: 1023px) {
  .container {
    width: 100%;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 5px;
  }
}

.button {
  cursor: pointer;
  appearance: none;
  background: transparent;
  padding: 0;
  border: none;
  transition: 0.3s ease-in-out;
}
.button--burger {
  display: none;
}
@media (max-width: 1279px) {
  .button--burger {
    background-image: url("../img/icons/icon-burger.svg");
  }
}
@media (max-width: 1279px) {
  .button--close {
    background-image: url("../img/icons/icon-close.svg");
  }
}
@media (max-width: 1279px) {
  .button--burger, .button--close {
    width: 34px;
    height: 34px;
    background-repeat: no-repeat;
    display: block;
    margin-left: auto;
    position: relative;
    z-index: 5;
  }
  .button--burger:hover, .button--close:hover {
    transition: 0.3s ease-in-out;
    background-color: #fed524;
  }
}
.button--submit {
  width: 286px;
  min-height: 53px;
  border: 2px solid #000000;
  border-radius: 10px;
  color: #000000;
  font-family: "Montserrat Alternates", "Arial", sans-serif;
  font-size: 16px;
  line-height: 20px;
  font-weight: 800;
  font-style: normal;
}
.button--submit:hover, .button--submit:focus-visible {
  border-color: #c06903;
  color: #c06903;
}
.button--submit:active {
  background-color: #fbdd51;
}
.button--order {
  font-family: "Montserrat Alternates", "Arial", sans-serif;
  font-size: 16px;
  line-height: 20px;
  font-weight: 700;
  font-style: normal;
  width: 353px;
  min-height: 64px;
  background-color: #fed524;
  color: #1d1d1e;
}
.button--order:hover, .button--order:focus-visible {
  background-color: #ffe266;
}
.button--order:active {
  color: #ffffff;
}
.button--swiper {
  width: 27px;
  height: 24px;
}
.button--swiper:disabled {
  visibility: hidden;
}
.button--swiper:hover svg {
  transition: 0.3s ease-in-out;
  transform: scale(1.5);
  --hover: #ffe266;
}

.swiper__control {
  width: 91px;
  height: 36px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: absolute;
  bottom: 7px;
  right: calc((100% - 1050px) / 2);
}

.blur {
  filter: blur(5px);
}